.footer-copyright {
  height: 40px;

  text-align: center;
  vertical-align: middle;

  display: flex;
  justify-content: center;
  align-items: center;
}

@media (min-width: $tablet-width) {
  .footer-copyright {
    padding-top: 5px;
    padding-left: 5px;

    grid-column-start: 3;
    grid-column-end: 4;
  }
}

.footer-copyright__text {
  padding-right: 12px;

  font-family: $font;
  font-size: 14px;
  line-height: 26px;
  color: $black;

  display: block;
}

@media (min-width: $tablet-width) {
  .footer-copyright__text {
    padding-left: 26px;
    padding-right: 12px;
  }
}

@media (min-width: $desktop-width) {
  .footer-copyright__text {
    padding-left: 48px;
  }
}

.footer-copyright__logo {
  margin: 0;
  padding: 0;
  font-size: 0;
}
